.navbar {
    background-color: #000;
    color: #1db954;
    padding: 10px;
    display: flex;
    align-items: center;
  }
  
  .navbar-logo {
    width: 50px;
    height: 50px;
    margin-right: 10px;
  }
  
  .navbar-nav {
    list-style-type: none;
    display: flex;
  }
  
  .navbar-nav li {
    margin-right: 10px;
  }
  
  .navbar-nav li a {
    color: #1db954;
    text-decoration: none;
  }
  
  .navbar-nav li a:hover {
    color: #fff;
  }
  