body {
    background-color: #121212;
    color: #ffffff;
    font-family: 'Roboto', sans-serif;
  }
  
  .lista-juegos {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  .item-juego {
    background-color: #1f1f1f;
    border-radius: 15px;
    overflow: hidden;
    transition: transform .2s;
  }
  
  .item-juego:hover {
    transform: scale(1.05);
  }
  
  .titulo-juego {
    font-size: 1.8em;
    margin: 0.8em;
    color: #1db954;
  }
  
  .imagen-juego {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .descripcion-juego {
    font-size: 1.2em;
    margin: 0.8em;
    line-height: 1.6;
  }
  